@import "./variables";

.mediaDetails {
  // background-color: grey;
  padding: 50;
  width: 70%;
  margin: 0 auto;
  .content {
    margin: 50px auto 50px auto;
    padding: 50px;

    .head {
      padding: 25px;
      background-color: #454545;
      box-shadow: 5px 5px #07070798;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-template-areas: "image detail detail detail";
      .imageWrapper {
        height: 500px;
        width: 350px;
        overflow: hidden;
      }
      img {
        height: 100%;
        grid-area: image;
      }

      .details {
        padding-left: 25px;
        text-align: left;
        grid-area: detail;

        .detailField {
          padding: 5px;
        }

        .title {
          margin: 0;
          font-size: 2.4em;
          margin-bottom: 25px;
        }

        .shortDescription {
          text-align: justify;
          font-style: italic;
          &::before {
            content: '"';
          }
          &::after {
            content: '"';
          }
        }
      }
    }
    .seasonsDisplay {
      background-color: #454545;
      box-shadow: 5px 5px #07070798;
      margin: 25px auto 25px auto;
      min-height: 300px;
    }

    .detailGroup {
      background-color: #454545;
      box-shadow: 5px 5px #07070798;
      margin: 25px auto 25px auto;
      padding: 25px;
      text-align: left;
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;
      justify-content: space-evenly;
      position: relative;

      .detailField {
        margin-right: auto;
      }

      .heading {
        position: absolute;
        top: 15px;
        left: 15px;
      }
    }

    .body {
      background-color: #454545;
      box-shadow: 5px 5px #07070798;
      margin: 25px auto 50px auto;
      min-height: 250px;
    }
  }

  .tabPanel {
    padding: 15px;
    text-align: left;
  }

  .tabs {
    padding: 15px;
    text-align: left;
  }
  .showButton {
    display: inline-block;
  }

  .formTextarea {
    width: 55em;
    height: 5em;
  }
  
  
  .MuiTypography-root.MuiTypography-body1 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  .editButton {
    display: flex;
    text-align: end;
  }
  .editButton:hover {
    background-color: #464646;
  }

  .accordionWrap{
    justify-content: center;
    width: 100%;
  }

  .MuiButtonBase-root.MuiButton-root.MuiButton-text {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
  }

  .titleWithEdit {
    display: flex;
    width:100%;
    justify-content: space-between;
  }

  #Layer_1 {
    fill: white;
    width: 25px;
    height: 25px;
    cursor: pointer;
  }

  .EditSeasonIcon {
    display: flex;
    justify-content: flex-end;
  }
  
}

.addMediumButton {
  position: absolute;
  bottom: 5px;
  right: 5px;
}